<template>
  <div id="box">
    <h1>My Skill List</h1>
    <div id="skillbox">
      <div :class="[{skilllist:true},{toggleStyle1:index%2==0},{toggleStyle2:index%2!=0}]"
           v-for="(skill,index) in skillcons">
        <div :class="[{skillItem:true},skill.idx,]"
             @mouseenter = "move(skill.idx,70)"
             @mouseleave = "move(skill.idx,10)">

          <img src="../assets/jpg/mark.png">
          <div class="p1">
            <h2>{{skill.skillName}}</h2>
            <p>{{skill.des}}</p>
          </div>

        </div>
      </div>
    </div>
  </div>

</template>

<style>
  @import '../assets/css/skillPage.css';
</style>

<script>
  import '../assets/js/jquery.js'
  export default{
    data(){
      return{
        skillcons:[
          {
            skillName:'HTML/CSS',
            des:`熟悉HTML和CSS，可以高效地根据设计图对静态页面进行布局，
            有响应式布局页面制作经验，可以设计适应不同设备屏幕的网页;对HTML5与CSS3有一定了解`,
            idx:'l1'
          },
          {
            skillName:'javaScript',
            des:`熟悉javaScript语言，熟练编写常见的动态效果和页面交互，熟悉DOM结构和相关操作`,
            idx:'l2'
          },
          {
            skillName:'jQuery',
            des:`熟悉jQuery库，可以自行编写常用插件`,
            idx:'l3'
          },
          {skillName:
            'PHP',
            des:`可以编写简单的ajax发送异步请求，完善页面和增进客户体验，了解php的基本语法`,
            idx:'l4'
          },
          {skillName:
            'Vue',
            des:`熟悉Vue框架，可以编写路由，完善页面框架，搭建项目脚手架`,
            idx:'l5'
          },
          {
            skillName: 'Webpack',
            des:`了解工程化与模块化编程，可以编写易维护、易拓展的代码，有webpack和git的使用经验`,
            idx:'l6'
          },

        ]
      }
    },
    methods:{
      toggle(){
        this.show = !this.show;
      },
      move(dom,value){
        $('.'+dom).stop(true).animate({
          bottom:value
        },500);
      }
    }

  }
</script>



