<template>
  <div id="box">
    <div :class="[{pic:true},{trans:uni.show},uni.bgpic]"
         @mouseenter="toggle(uni)"
         @mouseleave="toggle(uni)"
         v-for="(uni,index) in universities">
      <transition name="slide">
        <div class="uniDes" v-show="uni.show">
          <h2>{{uni.studyTime}}</h2>
          <p>
            &emsp;&emsp;{{uni.uniDes}}
          </p>
        </div>
      </transition>
    </div>
  </div>

</template>

<style scoped>
  #box{
    width:800px;
    height:1200px;
    margin:50px auto;
    background:#fafafa;
  }
  #box .pic{
    position:relative;

    height:300px;
    width:100%;
    margin:50px 0;
    /*background-size:100%;*/
    /*background-repeat:no-repeat;*/
    /*background-image:url("../assets/jpg/uni1.jpg");*/
  }
  #box .trans{
    opacity:0.75;
  }
  #box .uniDes{
    position:absolute;
    top:25px;
    left:200px;
    width:600px;
    height:100px;

  }
  #box .uniDes p{
    font-weight: 600;
  }

  .slide-enter-active,.slide-leave-active{
    transition:all 1s;
  }
  .slide-enter,.slide-leave-to{
    transform:translateX(-40px);
    opacity:0;
  }

  .bg1{
    background-image:url("../assets/jpg/uni1.jpg");
  }
  .bg2{
    background-image:url("../assets/jpg/uni2.jpg");
  }
  .bg3{
    background-image:url("../assets/jpg/uni3.jpg");
  }




</style>

<script>
  export default{
    data(){
      return{
        universities:[
          {
            studyTime:'本科：2011-2015',
            uniDes:`浙江师范大学前身为杭州师范专科学校，创建于1956年。1958年升格为杭州师范学院。
            1962年，杭州师范学院与浙江教育学院、浙江体育学院合并，更名为浙江师范学院。
            1965年，浙江师范学院从杭州搬迁至金华现校址。1980年被列为省属重点高校。
            1985年更名为浙江师范大学。2000年、2001年、2004年浙江财政学校、浙江幼儿师范学校和金华铁路司机学校相继并入。
            2015年列为浙江省首批重点建设高校。`,
            show:false,
            bgpic:'bg1'
          },
          {
            studyTime:'短期交换：2017-2018',
            uniDes:`甲南大学，英文：Konan University是一所位于日本兵库县神户市东滩区的私立大学，创立于1951年。
            校区位置在神户北部六甲山的山腰上面。甲南大学的学生人数将近一万人，该校不仅提供宽广并多样化的课程，并且透过「甲南国际交换中心」提供国际交换课程。`,
            show:false,
            bgpic:'bg2'
          },
          {
            studyTime:'硕士：2015-2018',
            uniDes:`厦门大学（Xiamen University），简称厦大（XMU），由著名爱国华侨领袖陈嘉庚先生于1921年创办，
            是中国近代教育史上第一所华侨创办的大学，也是国家“211工程”和“985工程”重点建设的高水平大学。`,
            show:false,
            bgpic:'bg3'
          }
        ]

      }
    },
    computed:{
      backPic(){
        return{
          backgroundImage:"url("+require('../assets/jpg/uni1.jpg')+")",
          background:yellow
        };
      }
    },
    methods:{
      toggle(obj){
        obj.show = !obj.show;
      }
    }

  }

</script>



